"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

const technicians = [
  { id: 1, name: "Alice Johnson" },
  { id: 2, name: "Bob Smith" },
  { id: 3, name: "Charlie Lee" },
];

const mockSchedules: Record<number, Record<string, string>> = {
  1: {
    Monday: "8am-4pm",
    Tuesday: "10am-6pm",
    Wednesday: "8am-4pm",
    Thursday: "Off",
    Friday: "8am-4pm",
  },
  2: {
    Monday: "Off",
    Tuesday: "8am-4pm",
    Wednesday: "8am-4pm",
    Thursday: "10am-6pm",
    Friday: "8am-4pm",
  },
  3: {
    Monday: "8am-4pm",
    Tuesday: "Off",
    Wednesday: "10am-6pm",
    Thursday: "8am-4pm",
    Friday: "8am-4pm",
  },
};

const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"];

export default function Technicians() {
  const [selected, setSelected] = useState<number[]>([technicians[0].id]);

  const toggleTechnician = (id: number) => {
    setSelected((prev) =>
      prev.includes(id) ? prev.filter((tid) => tid !== id) : [...prev, id]
    );
  };

  return (
    <div className="flex gap-6 p-6">
      {/* Calendar */}
      <Card className="flex-1 p-6">
        <h2 className="text-xl font-semibold mb-4">Weekly Schedule</h2>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Technician</TableHead>
              {days.map((day) => (
                <TableHead key={day}>{day}</TableHead>
              ))}
            </TableRow>
          </TableHeader>
          <TableBody>
            {selected.map((tid) => {
              const tech = technicians.find((t) => t.id === tid);
              const sched = mockSchedules[tid];
              return (
                <TableRow key={tid}>
                  <TableCell className="font-medium">{tech?.name}</TableCell>
                  {days.map((day) => (
                    <TableCell key={day} className="text-center">
                      {sched[day]}
                    </TableCell>
                  ))}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </Card>
      {/* Technician List */}
      <Card className="w-64 p-6 flex-shrink-0">
        <h3 className="text-lg font-semibold mb-4">Technicians</h3>
        <ul className="space-y-4">
          {technicians.map((tech) => (
            <li key={tech.id} className="flex items-center gap-3">
              <Checkbox
                checked={selected.includes(tech.id)}
                onCheckedChange={() => toggleTechnician(tech.id)}
                id={`tech-${tech.id}`}
              />
              <label htmlFor={`tech-${tech.id}`} className="cursor-pointer">
                {tech.name}
              </label>
            </li>
          ))}
        </ul>
      </Card>
    </div>
  );
}
